// src/App.js
import React from 'react';
import ViewManager from './components/ViewManager';
import ResponsiveHeader from './components/ResponsiveHeader';
import BottomNavigation from './components/BottomNavigation';
import DataSourceIndicator from './components/DataSourceIndicator';
import FilterDebugPanel from './components/FilterDebugPanel'; // 
import { useAppState } from './hooks/useAppState';

const App = () => {
  // useAppState
  const appState = useAppState();
  const {
    // 
    currentView,
    compareList,
    favoriteUniversities,
    
    // Firebase
    universitiesLoading,
    universitiesError,
    universities,
    
    // 
    actions,
    
    // ViewManager
    state,
    data
  } = appState;

  // Firebase 
  if (universitiesError && universities.length === 0) {
    return (
      <div className="min-h-screen bg-gray-50 flex items-center justify-center">
        <div className="bg-white p-8 rounded-xl shadow-lg max-w-md mx-4">
          <div className="text-center">
            <div className="text-red-500 mb-4">
              <svg className="w-16 h-16 mx-auto" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 8v4m0 4h.01M21 12a9 9 0 11-18 0 9 9 0 0118 0z" />
              </svg>
            </div>
            <h2 className="text-xl font-bold text-gray-800 mb-2"></h2>
            <p className="text-gray-600 mb-4 text-sm">
              Firebase 
            </p>
            <div className="bg-gray-100 p-3 rounded-lg mb-4 text-xs text-left">
              <p className="font-semibold mb-1">:</p>
              <p className="text-red-600">{universitiesError}</p>
            </div>
            <div className="space-y-2">
              <button 
                onClick={() => window.location.reload()}
                className="w-full bg-green-600 hover:bg-green-700 text-white px-4 py-2 rounded-lg transition-colors"
              >
                
              </button>
              <button 
                onClick={actions.refetchUniversities}
                className="w-full bg-gray-600 hover:bg-gray-700 text-white px-4 py-2 rounded-lg transition-colors"
              >
                
              </button>
            </div>
            <div className="mt-4 text-xs text-gray-500">
              <p> REACT_APP_FIREBASE_API_URL </p>
            </div>
          </div>
        </div>
      </div>
    );
  }

  // Firebase 
  if (universitiesLoading && universities.length === 0) {
    return (
      <div className="min-h-screen bg-gray-50 flex items-center justify-center">
        <div className="bg-white p-8 rounded-xl shadow-lg max-w-md mx-4">
          <div className="text-center">
            <div className="text-green-600 mb-4">
              <svg className="w-16 h-16 mx-auto animate-spin" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 4v5h.582m15.356 2A8.001 8.001 0 004.582 9m0 0H9m11 11v-5h-.581m0 0a8.003 8.003 0 01-15.357-2m15.357 2H15" />
              </svg>
            </div>
            <h2 className="text-xl font-bold text-gray-800 mb-2"></h2>
            <p className="text-gray-600 mb-4 text-sm">
              Firebase ...
            </p>
            <div className="bg-gray-200 rounded-full h-2 overflow-hidden">
              <div className="bg-green-600 h-full rounded-full animate-pulse w-3/4"></div>
            </div>
            <div className="mt-4 text-xs text-gray-500">
              <p></p>
            </div>
          </div>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gray-50">
      {/*  */}
      <ResponsiveHeader 
        currentView={currentView}
        onChangeView={actions.changeView}
      />

      {/*  */}
      <DataSourceIndicator 
        universities={universities}
        loading={universitiesLoading}
        error={universitiesError}
        onRefresh={actions.refetchUniversities}
        showDetails={process.env.NODE_ENV === 'development'}
      />

      {/*  */}
      {process.env.NODE_ENV === 'development' && universities.length > 0 && (
        <FilterDebugPanel universities={universities} />
      )}

      {/*  */}
      <main className="container mx-auto p-4 pt-16 pb-20">
        <ViewManager 
          currentView={currentView}
          data={data}
          handlers={actions}
        />
      </main>
      
      {/*  */}
      <BottomNavigation 
        currentView={currentView}
        onChangeView={actions.changeView}
        favoriteUniversities={favoriteUniversities}
        compareList={compareList}
      />
      
      {/*  */}
      <footer className="bg-gray-800 text-white p-6 mt-8 mb-16">
        <div className="container mx-auto text-center">
          <p> 2025 </p>
          <p className="text-sm mt-2"></p>
        </div>
      </footer>
    </div>
  );
};

export default App;