// src/hooks/useScrollDirection.js ()
import { useState, useEffect } from 'react';

/**
 * 
 * @param {number} threshold - px
 * @returns {object} - { scrollDirection, isVisible }
 */
const useScrollDirection = (threshold = 5) => {
  const [scrollDirection, setScrollDirection] = useState('up');
  const [isVisible, setIsVisible] = useState(true);
  const [lastScrollY, setLastScrollY] = useState(0);

  useEffect(() => {
    let ticking = false;

    const updateScrollDirection = () => {
      const currentScrollY = window.pageYOffset || document.documentElement.scrollTop;
      
      // 50px
      if (currentScrollY < 50) {
        setIsVisible(true);
        setLastScrollY(currentScrollY);
        ticking = false;
        return;
      }

      // 
      if (Math.abs(currentScrollY - lastScrollY) < threshold) {
        ticking = false;
        return;
      }

      // 
      if (currentScrollY > lastScrollY) {
        // 
        if (scrollDirection !== 'down') {
          setScrollDirection('down');
          setIsVisible(false);
        }
      } else {
        // 
        if (scrollDirection !== 'up') {
          setScrollDirection('up');
          setIsVisible(true);
        }
      }

      setLastScrollY(currentScrollY);
      ticking = false;
    };

    const handleScroll = () => {
      if (!ticking) {
        requestAnimationFrame(updateScrollDirection);
        ticking = true;
      }
    };

    // 
    setLastScrollY(window.pageYOffset || document.documentElement.scrollTop);

    window.addEventListener('scroll', handleScroll, { passive: true });

    // 
    return () => {
      window.removeEventListener('scroll', handleScroll);
    };
  }, [scrollDirection, lastScrollY, threshold]);

  return {
    scrollDirection,
    isVisible
  };
};

export default useScrollDirection;